import React from 'react'
import img2 from "../img/img2.jpg"
import "./Home.css"
import { useState, useEffect } from "react";
import { SocialIcon } from 'react-social-icons';
import { useNavigate,Link,Navigate } from 'react-router-dom';
import axios from "axios"
import Coin from '../routes/Coin';
import CoinItem from './CoinItem';

const Home = () => {

    const [coins, setCoins] = useState([])
    //   const [state,setState]=useState(false);
    const navigate1 = useNavigate();
    const handleClick = () => {
        navigate1('/Coins');
        console.log("press")
    };

    const navigate2=useNavigate()
    const seehistory=()=>{
        // <Link to={`/coin/${coins.id}`} element={<Coin />} key={coins.id}>
        //                     <CoinItem coins={coins} />
        // </Link>
        navigate2('/Coins')
        // <Navigate to={`/coin/${coins.id}`} state={{ coins }} />
    }


    let foourl = "https://mdbootstrap.com/";

    const url = 'https://api.coingecko.com/api/v3/coins/markets?vs_currency=usd&order=market_cap_desc&per_page=6page=1&sparkline=false'

    useEffect(() => {
        axios.get(url).then((response) => {
            setCoins(response.data)
            // console.log(response.data[0])
        }).catch((error) => {
            console.log(error)
        })
    }, [])

    const purple = {
        color: '#6900ff',
    }
    const bgc = {
        backgroundColor: '#26272b',
    }
    return (
        <>
            <section >
                <div className='container-1'>
                    <img className='images-btc' src={img2} />
                    <div class="centered">The World's largest trading website</div>
                </div>

            </section>

            <section className="LearnMore bg-dark text-white">
                <div className="head" >
                    <h1>Buy crypto at true cost</h1>
                </div>
                {coins.map(coins => {
                    return (
                        <div className="coin-cont" onClick={seehistory}>

                            <div className='coin-row'>
                                <p>{coins.market_cap_rank}</p>
                                <div className='img-symbol'>
                                    <img className="images" src={coins.image} alt='' />
                                    <p>{coins.symbol.toUpperCase()}</p>
                                </div>
                                <p>${coins.current_price.toLocaleString()}</p>
                                <p>{coins.price_change_percentage_24h.toFixed(2)}%</p>
                                <p className='hide-mobile'>${coins.total_volume.toLocaleString()}</p>
                                <p className='hide-mobile'>${coins.market_cap.toLocaleString()}</p>
                            </div>

                        </div>

                    )
                })}

                <div className="btn">
                    <button type="button" className="btn btn-light " onClick={handleClick}>Learn Mores</button>


                </div>
            </section>

            <section>
                <footer className="bg-dark text-center text-white ">
                    {/* Grid container */}
                    <div className=" container p-4 pb-2 ">
                        {/* Section: Social media */}
                        <section className="mb-5 pl-4 footr1">
                            {/* Facebook */}
                            <SocialIcon url="https://twitter.com/jaketrent" />
                            <SocialIcon url="https://facebook.com/jaketrent" />
                            <SocialIcon url="https://whatsapp.com/jaketrent" />
                            {/* <SocialIcon url="https://github.com/jaketrent" /> */}
                            <SocialIcon url="https://linkedin.com/jaketrent" />
                            <SocialIcon url="https://instagram.com/jaketrent" />

                        </section>
                        {/* Section: Social media */}
                    </div>
                    {/* Grid container */}

                    {/* Copyright */}
                    <div className="text-center p-3 footr2" style={bgc}>
                         2023 Copyright:
                        <a className="text-white" href={foourl}>Managed by Anas Malik</a>
                    </div>
                    {/* Copyright */}
                </footer>


            </section>
        </>



    )

}

export default Home